/*--------------------------------------------------------------
# Inner Circle - Single Post

1. Override existing styles from parent theme SASS
2. File attachment
--------------------------------------------------------------*/

@import "../node_modules/@asu-design-system/design-tokens/build/scss/_variables"; 
@import "media-queries";

@mixin small-horiz-divider {
    content: '';
    height: 1px;
    width: 100%;
    max-width: $uds-size-spacing-32;
    display: block;
    position: absolute;
    top: 0;
    background-color: $uds-color-base-gray-3;
}

// Removes default padding on mobile in favor of applying padding to a container.
.single main {
    padding: 0;
}

/*--------------------------------------------------------------
5. Hero + title layout
--------------------------------------------------------------*/

// Mobile
.single-hero {

    margin-bottom: 2rem;

    img.wp-post-image {
        // padding: $uds-size-spacing-2 0;
        max-height:300px;
        object-fit: cover;
        width:100%;

        &.featured {
            max-height:515px;
        }
    }

    .category-wrap {
        background-color: #ffc627;
        height:3rem;
        padding: 0 1.25rem; // Cheating because of the btn class on the link.

        .category-tags .btn {
            margin: .75rem 1rem 0 0;
            background-color: #ffc627;
            text-transform: uppercase;
        }
    }

    .title-wrap {

        background-color: #191919;
        color:#ffffff;
        padding: 2rem 2rem 1rem 2rem;

        h1 {
            /* Like an H2, mobile */
            margin:0;
            font-size: 2rem;
            line-height: 2.125rem;
            letter-spacing: -0.07rem;
        }
    }

    .tag-wrap {
        background-color: #191919;
        color: #ffffff;
        padding: 1rem 2rem 1rem 2rem;
        
        .entry-meta {
            margin-bottom: 1rem;
        }

        .tags-links {
            margin-bottom: 0;
            a {
                color:#ffffff;
            }
        }

        svg {
            margin-right:$uds-size-spacing-1;
        }
    }
}

// Desktop
@include media-breakpoint-up($lg) {
    .single-hero {

        display:grid;
        grid-template-columns: 66.666% 1fr;
        grid-template-rows: 1fr auto repeat(2, 3rem);

        img.wp-post-image {
            grid-row: 1 / span 2;
            grid-column: 1 / span 2;
        }

        .category-wrap {
            grid-row: 3;
            grid-column: 2;

            background-color: #ffc627;
            text-align:right;
            height:3rem;

            .category-tags .btn {
                margin-right:1rem;
                margin-bottom:0;
                background-color: #ffc627;
                text-transform: uppercase;
            }
        }

        .title-wrap {
            grid-row: 2;
            grid-column: 1;
            height: auto;

            background-color: rgba(0,0,0,.4);

            border-top: 2px solid $uds-color-base-white;
            border-right: 2px solid $uds-color-base-white;

            h1 {
                /* Like an H2 */
                margin:0;
                font-size: 2.5rem;
                line-height: 2.75rem;
                letter-spacing: -.0875rem;
            }
        }

        .tag-wrap {
            grid-row: 3 / span 2;
            grid-column: 1;

            border-right: 2px solid $uds-color-base-white;

            .entry-meta {
                margin-bottom: .5rem;
            }
        }

        &.no-image {
            grid-template-columns: 75% 1fr;
            grid-template-rows: auto 3rem auto;

            .category-wrap {
                grid-row: 1;
                margin-top: 3rem;
            }

            .title-wrap {
                grid-row: 1 / span 2;
                background-color: #191919;
            }

            .tag-wrap {
                grid-row: 3;
                padding-bottom: 2rem;
            }
        }
    }
}

/*--------------------------------------------------------------
5. content-wrap, adjust for desktop layout.
--------------------------------------------------------------*/
@include media-breakpoint-up($lg) {
    main > .container.content-wrap {
        padding-left:0;
        padding-right:0;
    }
}

